import React from "react";

export interface GaugebarData {
  max: number;
  value: number;
  color?: string;
}

function Gaugebar(props: GaugebarData) {
  // TODO: 컴포넌트 옮기기
  return (
    <div className="w-full bg-gray-300 rounded-full h-2">
      <div
        className={`${
          props.color ? props.color : "bg-red-500"
        } h-2 rounded-full`}
        style={{
          width:
            props.value < 10
              ? props.value === 0
                ? props.value
                : 8
              : props.value + `%`,
        }}
      ></div>
    </div>
  );
}

export default Gaugebar;
